<template>
    <div>
        <app-header></app-header>
        <div class="miniheader">
            <div class="container">
                <div class="miniheader-logo"><a href="#">小米官网</a></div>
                <div class="miniheader-title ">
                    <h2>我的购物车</h2>
                    <p>温馨提示：产品是否购买成功，以最终下单为准哦，请尽快结算</p>

                </div>
                <div class="miniheader-info">
                    <i class="iconfont">&#x221a;</i>
                    <a href="#" class="user-name"><span></span></a>
                    <span class="sep">|</span>
                    <a href="#" class="info-order">我的订单</a>
                </div>
            </div>
        </div>
        <div class="cartmain" v-if="shopCartContent.length>0">
            <div class="container">
                <div class="cartmain-list">
                    <div class="list-head clearfix">
                        <div class="col col-check"><i class="iconfont icon-checkbox"  @click="checkAll()" :class="{'select':isSeletedAll}">&#x221a;</i> 全选</div>
                        <div class="col col-img">&nbsp;</div>
                        <div class="col col-name">商品名称</div>
                        <div class="col col-price">单价</div>
                        <div class="col col-num">数量</div>
                        <div class="col col-total">小计</div>
                        <div class="col col-action">操作</div>
                    </div>
                    <div class="list-body">
                        <div class="item-box clearfix" v-for="(val,index) in shopCartContent" :key="index"  :id="val.id" >
                            <div class="col col-check"><i class="iconfont icon-checkbox" @click="check(val)" :class="{'select':val.isSelected}">&#x221a;</i></div>
                            <div class="col col-img"><a :href="val.imgs[0]"><img :src="val.imgs[0]"  style="width: 100px; height: 100px"></a></div>
                            <div class="col col-name">{{val.name}}</div>
                            <div class="col col-price"><label class="price">{{val.price}}</label>元</div>
                            <div class="col col-num">
                                <div class="change-num clearfix">
                                    <a data-change="-1" href="javascript:;" @click="updateCart(val,-1)"><i class="iconfont">&#xe60b;</i></a>
                                    <input type="text" :value="val.quantity">
                                    <a data-change="1" href="javascript:;" @click="updateCart(val,1)"><i class="iconfont">&#xe609;</i></a>
                                </div>
                            </div>
                            <div class="col col-total"><label class="price">{{val.price*val.quantity}}</label>元</div>
                            <div class="col col-action"><a href="javascript:;" @click="del(val,0)"><i class="iconfont">&#xe602;</i></a></div>
                        </div>
                    </div>
                </div>
                <div class="cartmain-bar clearfix">
                    <div class="bar-info">
                        <a href="#">继续购物</a>
                        <span class="info-total">共 <i class="total-num">{{totalNum}}</i> 件商品，已选择 <i class="total-num">{{selectedNum}}</i> 件</span>
                    </div>
                    <span class="total-price">合计：<em class="totalprice-num">{{total}}</em>元</span>
                        <button  class="bar-btn btn-primary "  @click="ddd" :disabled="selectedId.length==0?true:false" :class="btndis" >去结算</button>
                </div>
            </div>
        </div>
        <div v-else class="emptyCar">
            <div class="content">
                <h1>购物车空空如也</h1>
                <div class="buttonGroup" v-if="isLogin">
                    <router-link class="goShopBt" :to="{path:'/product',query:{pid:1}}">马上去购物</router-link>
                </div>
                <div class="buttonGroup " v-else>
                    <router-link  class="loginBt" :to="'/login'">立即登录</router-link>
                    <router-link class="goShopBt" :to="{path:'/product',query:{pid:1}}">马上去购物</router-link>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import appHeader from "../components/Miheader.vue"
    import {mapState} from "vuex";
    import ElCheckbox from "../../node_modules/element-ui/packages/checkbox/src/checkbox";
    import Vue from "vue"
    import {api} from "../utils/api"
export default{
    created(){
        this.$store.dispatch("render/getIsHome",false);
        this.$store.dispatch("userMs/getShopCart");
      },
    data(){
        return{
            name:'appCheckbox',
            event:[],
            selectedId:[],
            selectedNum:0,//选择商品数
            total:0 ,//总钱数
            totalNum:0, //总商品数
            isSeletedAll:false
        }
    },
    components: {
            ElCheckbox,
            appHeader,
        },

    computed: {
        ...mapState("userMs", ["shopCartContent", "ShopCartNum","isLogin"]),
        ...mapState("render",['isHome']),
        btndis(){
            return{
                "btn-disabled":this.selectedId.length===0,
            }
        },

    },
    methods:{
        del(val,num){
            this.delSelectedId(val);
            let data={
                id:0,
                quantity:0
            };
                data.id=val.id;
                data.quantity=num;
                this.shopCartContent.forEach((v,k)=>{
                    if(v.id===val.id){
                        this.shopCartContent.splice(k,1)
                    }
                });
                this.cal();
                this.$store.dispatch("userMs/changeShopCart",data);
        },
        updateCart(val,num){
            let data={
                id:0,
                quantity:0
            };
            val.quantity=+val.quantity+num;
           if(val.quantity<1){
               data.id=val.id;
               data.quantity=0;
               this.shopCartContent.forEach((v,k)=>{
                   if(v.id===val.id){
                       this.shopCartContent.splice(k,1)
                   }
               });
               this.delSelectedId(val);
               this.cal()
           }else {
               data.id=val.id;
               data.quantity=val.quantity;
           }
            this.cal();
            this.$store.dispatch("userMs/changeShopCart",data);
        },
        check(val){
            if(val.isSelected){
                val.isSelected=false;
                this.isSeletedAll=false;
                this.selectedId.forEach((v,k)=>{
                    if(v===val.id){
                        this.selectedId.splice(k,1)
                    }
                })

            }else{
                val.isSelected=true;
                this.selectedId.push(val.id);
                this.shopCartContent.forEach(v=>{
                        if(v.isSelected){
                            this.isSeletedAll=true
                        }
                    })
            }
            this.cal()
        },
        delSelectedId(val){
            if(this.selectedId.length>0){
                this.selectedId.forEach((v,k)=>{
                    if(v===val.id){
                        this.selectedId.splice(k,1)
                    }
                })
            }

        },
        cal(){
            let total=0;
            let selectedNum=0;
            let totalNum=0;
            this.shopCartContent.forEach((v,k)=>{
                if(v.isSelected){
                    total+= (+v.price*+v.quantity);
                    selectedNum+=(+v.quantity)
                }
                totalNum+=(+v.quantity);
            });
           this.total=total;
           this.selectedNum=selectedNum;
           this.totalNum=totalNum;
        },
        ddd(){
            this.$router.push({ path: '/checkout', query: { ids: this.selectedId.toString() }})
        },
        checkAll(){
            if(this.isSeletedAll){
                this.isSeletedAll=false;
                this.shopCartContent.forEach(v=>{
                    v.isSelected=false;
                })
                this.cal();
                this.selectedId=[];
            }else {
                this.isSeletedAll=true;
                this.shopCartContent.forEach(v=>{
                    v.isSelected=true;
                    this.selectedId.push(v.id);
                })
                this.cal();
                console.log(this.selectedId)
            }
        }
    }
}
</script>
<style scoped lang="scss">
    @import "../css/style.css";
    @import "../css/cart.css";
    @import "../css/iconfont.css";
    .cartmain-bar .btn-disabled{
        cursor: not-allowed;
    }
    .emptyCar{
        background-color: #f5f5f5;
        height: 600px;
        padding-top: 60px;
        .content{
            height: 273px;
            color: #b0b0b0;
            padding-left: 700px;
            margin: 65px 0 130px auto;
            background: url("../img/cart-empty.png") no-repeat 500px 0;
            overflow: hidden;
            text-align: center;
            h1{
                font-size: 50px;
            }
            .buttonGroup{
                height: 100%;
                padding-top: 30px;
                a{
                    margin-top: 50px;
                    display: inline-block;
                    width: 170px;
                    height: 48px;
                    background-color: #f25807;
                    line-height:48px ;
                    color: white;
                    font-size: 16px;
                }
                a:nth-child(2){
                    margin-left: 30px;
                }
            }
        }

    }


</style>