<template>
    <div>
        <div class="miniheader">
            <div class="container">
                <div class="miniheader-logo"><router-link to="/" type="a">小米官网</router-link></div>
                <div class="miniheader-title ">
                    <h2>支付订单</h2>
                    <p>温馨提示：产品是否购买成功，以最终下单为准哦，请尽快结算</p>

                </div>
                <div class="miniheader-info">
                    <a href="#" class="user-name"><span></span></a>
                    <span class="sep">|</span>
                    <a href="#" class="info-order">我的订单</a>
                </div>
            </div>
        </div>
        <div class="confirm">
            <div class="container">
                <form action="post">
                    <div class="confirm-order confirm-section clearfix">
                        <i class="iconfont">&#x221a;</i>
                        <div class="box-order clearfix">
                            <div class="order-info">
                                <h2 class="info-title">订单提交成功！去付款咯～</h2>
                                <p class="info-time">请在<span>0小时30分</span>内完成支付, 超时后将取消订单</p>
                                <p class="info-address">收货信息：<span class="address">
                                   姓名：{{aboutUserMs.recipient}}&nbsp
                                   电话：{{aboutUserMs.phone}}&nbsp
                                    地址： {{aboutUserMs.area}}&nbsp
                                     {{aboutUserMs.address}}&nbsp
                                </span></p>
                            </div>
                            <div class="order-total">
                                <p>应付总额：<span><em class="money">{{totalMoney}}</em>元</span></p>
                                <a href="javascript:;">订单详细</a>
                            </div>
                        </div>

                        <div class="order-detail">
                            <ul>
                                <li class="clearfix">
                                    <div class="label">订单号：</div>
                                    <div class="content order-no">{{order_no}}</div>
                                </li>
                                <li class="clearfix">
                                    <div class="label">收货信息：</div>
                                    <div class="content address">
                                        姓名：{{aboutUserMs.recipient}}&nbsp
                                        电话：{{aboutUserMs.phone}}&nbsp
                                        地址： {{aboutUserMs.area}}&nbsp
                                        {{aboutUserMs.address}}&nbsp</div>
                                </li>
                                <li class="clearfix">
                                    <div class="label">商品名称：</div>
                                    <div class="content items" v-for="(v,k) in aboutShop">{{v.name}}&nbspX&nbsp{{v.quantity}}</div>
                                </li>

                            </ul>
                        </div>
                    </div>

                    <div class="confirm-payment confirm-section">
                        <div class="payment-title">选择以下支付方式付款</div>
                        <div class="payment-list">
                            <ul class="clearfix">
                                <li id="pay"><img src="../img/weixinpay.png" alt="" @click="pay"></li>
                                 <li><img src="../img/payOnline_zfb.png" alt=""></li>
                                <!-- <li><img src="img/unionpay.png" alt=""></li> -->
                            </ul>

                        </div>
                    </div>
                </form>

            </div>

            <div class="confirm-qrcode" v-show="isOpenModal" @click="isOpenModal=false">
                <div id="qrcode"></div>
            </div>
        </div>
    </div>
</template>
<script>
import QRCode from 'qrcodejs2';
import {api} from "../utils/api"
import {mapState} from "vuex"
export default{
    created(){
        this.aboutShop = this.ms.selectShopMs;
        this.aboutUserMs = this.ms.userAdMs;
        this.order_no=this.$route.query.data;
        for (var i = 0; i < this.aboutShop.length; i++) {
            this.totalMoney += (this.aboutShop[i].price * +this.aboutShop[i].quantity);
        }

    },
    computed: {
        ...mapState("selectComfirm",["ms"])
    },
    data(){
        return {
            aboutShop: [],
            aboutUserMs: [],
            totalMoney: 0,
            isOpenModal: false,
            order_no:0,
        }
    },
    methods: {
        pay(){
            this.isOpenModal=true;
            api.post("/user/pay", {order_no:this.order_no}).then(response => {
                console.log(response)
                let qrcode = new QRCode('qrcode', {
                    width: 300,
                    height: 300,
                    text: response,
                    colorDark: "#000",
                    colorLight: "#fff",
                })
            });
        }
    }
}
</script>
<style >
    @import "../css/style.css";
    @import "../css/confirm.css";
    @import "../css/iconfont.css";
</style>